.movie-preview {
    width: 300px;
    background-color: #ffffff;
    border-radius: 12px;
    border: 3px solid #e0e0e0;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1); 
    overflow: hidden;
    transition: all 0.3s ease;
    text-align: center;
    cursor: pointer; 
}

.movie-preview:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.movie-preview .title {
    font-weight: bold;
    color: #333;
    margin: 10px 0; 
}

.movie-preview .description {
    color: #666;
    margin-bottom: 10px; 
}

.movie-preview img {
    width: 100%;
    height: auto;
    border-radius: 12px 12px 0 0;
    object-fit: cover;
}







